.Menu-Slider-Logged {

    width: 100%;
    height: 60px;
    overflow: hidden;
    position: relative;

    padding-inline: 5%;
}




.Menu-Inner {
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: space-between;
    align-items: center;

    overflow-x: scroll;

    gap: 8px;

    scrollbar-width: none;
    scrollbar-color: transparent transparent;
}
.Menu-Inner::-webkit-scrollbar {
    width: 0;
    height: 0;
  }


.Menu-Inner li {
    display: flex;
    align-items: center;
    justify-content: left;
    list-style: none;
    gap: 8px;
}

.Menu-Inner li a {
    text-decoration: none;
    white-space: nowrap;

    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #807E81;
}

.Menu-Inner li svg {
    display: flex;
    align-items: center;
    justify-content: center;


}

.Menu-Inner li {
    outline: none;
    cursor: pointer;
    user-select: none;
}

.Menu-Inner li svg path {
    fill: #737174
}

.Menu-Inner li:active svg path {
    fill: #E8ECEF;
}

.Menu-Inner li:active a {
    color: #E8ECEF;

}


.NavLinks:hover{
    color: #E8ECEF;
}

.NavLinks:hover svg path{
    fill: #E8ECEF;
    color: #E8ECEF;
}

.NavLinks path{
    transition: all 0.5s ease-in-out;
}


@media screen and (min-width: 1280px) {
    .Menu-Slider-Logged {
        display: none;
    }
}