.BackgroundComponent-Container {
    width: 100%;
    height: clamp(440px, 50vw, 720px);

    overflow: hidden;

    position: relative;
    display: flex;
    flex-direction: column;
}

.Bg-filter {
    user-select: none;
    z-index: 11;
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    background: linear-gradient(172.23deg, #000000b2 3%, rgba(0, 0, 0, 0.61) 30%, rgba(0, 0, 0, 0.322) 50%);
    mix-blend-mode: multiply;
}


.Img-container {
    width: 100%;
    height: 100%;
    z-index: 11;
    position: relative;
}

.Img-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.BGground{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0;
    top: 0;

}

@media screen and (min-width: 1280px) {
    .BackgroundComponent-Container .Menu-Slider {
        display: none;
    }
}