/* default style open */

.Navigation {

    width: 100%;

    height:  var(--nav-height);
 
    background-color: #0A070B;

    display: flex;
    justify-content: space-between;
    align-items: center;

    gap: 24px;

    padding-left: clamp(20px, 5vw, 70px);
    padding-right: clamp(20px, 5vw, 70px);
}



/* Logo Navigation */

.NavLogo{
    order: 1;
}
.Navigation .NavLogo .LogoIcon{
    width: clamp(80px, 20vw, 150px);
}



/* Lista de navegação */

.NavigationLinks{
    order: 2;

    display: flex;

    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
}

.NavigationLinks li {
    user-select: none;

}
.NavigationLinks li svg path {
    fill: #737174;
}

.NavigationLinks li:active svg path,
.NavigationLinks li:active a{
    fill: #4F4E50;
    color: #4F4E50;
}

.NavigationLinks li svg path,
.NavLinks{
    transition: all 0.3s ease-in-out;
}

.NavigationLinks li:hover svg path,
.NavigationLinks li:hover a{
    fill: #E8ECEF;
    color: #E8ECEF;
}

.NavLinks{
    display: flex;
    align-items: center;
    align-items: center;
    justify-content: center;
    list-style: none;

    gap: 8px;

    text-decoration: none;
    white-space: nowrap;

    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #807E81;

}
.NavLinks svg {
    display: flex;
    align-items: center;
    justify-content: center;
}








/* Botão Login */


.sing-btn {
    min-width: 59px;
    min-height: 25px;
    width: clamp(59px, 10vw, 98px);
    height: clamp(25px, 5vw, 42px);


    background: #F41B3B;
    border-radius: 3px;
    border: none;

    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    text-decoration: none;
}
.sing-btn p {
    user-select: none;

    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: clamp(12px, 2vw, 16px);
    line-height: 22px;
    color: #fff;
}

.sing-btn:active {
    background-color: #a7142a;
}

.signin-container {
    margin-left: auto;
    position: relative;
}

.signin-container span {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.signin-container span svg {
    display: flex;
    align-items: center;
    justify-content: center;
}

.pesquisa-container {
    display: none;
}

.nav-login {
    position: relative;
    
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    
    order: 3;
}

/* default style  close */









/*      MobileFirst 
media query MobileFirst Open*/

@media screen and (max-width: 480px) {
    .Navigation {
        display: flex;
        flex-direction: row;


    }


    .Navigation ul {

        display: none;

        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        gap: 20px;
    }

    .Navigation ul li {
        list-style: none;
    }

    .Navigation ul li a {

        color: #807E81;
    }


}

