/*      Destok/ Large Screens
media query MobileFirst Open*/

@media screen and (min-width: 1025px) {



    .Navigation ul {
        padding: 20px;



        flex-direction: row;
        align-items: center;
        gap: 24px;
    }

    .Navigation ul li a {
        text-align: center;

        cursor: pointer;

        color: #807E81;

    }

    .Navigation ul li {
        cursor: pointer;
        height: 22px;


        display: flex;
        align-items: center;
        align-items: center;
        justify-content: center;
        list-style: none;

        gap: 8px;
    }

    .Navigation ul li svg {
        align-self: center;
        display: flex;
        align-items: center;
        justify-content: center;
    }



}

@media screen and (max-width: 1279px) {
    .Navigation ul {
        padding: 20px;

        display: none;

        flex-direction: row;
        align-items: center;
        gap: 24px;
    }

    .logo-container {
        order: 1;
        flex-basis: 20%;
        min-width: 106px;
        min-height: 28px;

        height: 0;
        display: flex;
        align-items: center;


    }
}


@media screen and (min-width: 1280px) {

    .navNormal .Navigation {
        background: none;
        position: absolute;
    }
    .navLogada .Navigation {
        background: none;
        position: relative;
    }
    .navLogada .Navigation {
        background: none;
        position: relative;
    }

    .Navigation ul {
        padding: 20px;

        display: flex;

        flex-direction: row;
        align-items: center;
        gap: 24px;
    }

    .logo-container {
        order: 1;

        max-width: 169px;
        max-height: 41px;

        height: 0;
        display: flex;
        align-items: center;
    }
}


